/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import {
	PanelBody,
	ToggleControl,
	TextControl,
	RadioControl,
	BaseControl,
	ButtonGroup,
	Button,
} from '@wordpress/components';
import { useDispatch } from '@wordpress/data';
// import { createInterpolateElement } from '@wordpress/element';

/**
 * @Internal dependencies
 */
import { arkbGlbStr } from '@helper';
import { ArkbProOnly, ArkbDeviceTab } from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * 設定データ
 */
const TAB_WIDTH_OPTIONS = [
	{
		label: __('Fit to text', 'arkhe-blocks'),
		value: 'auto',
	},
	{
		label: __('Equal width', 'arkhe-blocks'),
		value: 'equal',
	},
];

const TAB_ALIGNS = [
	{
		value: 'left',
		label: __('Left', 'arkhe-blocks'),
	},
	{
		value: '',
		label: __('Center', 'arkhe-blocks'),
	},
	{
		value: 'right',
		label: __('Right', 'arkhe-blocks'),
	},
];

/**
 * sidebar
 */
export default ({ attributes, setAttributes, clientId }) => {
	const {
		// anchor,
		tabId,
		activeTab,
		tabHeaders,
		tabWidthPC,
		tabWidthSP,
		isScrollPC,
		isScrollSP,
		tabAlign,
	} = attributes;

	const { getBlockOrder } = wp.data.select('core/block-editor');
	const { updateBlockAttributes } = useDispatch('core/block-editor');

	return (
		<>
			<div className='arkb-helpPanel'>
				{__(
					'The {i}-th tab of this block will be displayed open when the URL contains the following parameters.',
					'arkhe-blocks'
				)}
				<input
					readOnly
					type='text'
					value={`?arkb-tab=${tabId}-{i}`}
					className='components-text-control__input arkb-helpInput u-arkb--mt--5'
				/>
				<div className='arkb-helpText u-arkb--mt--5'>
					{arkbGlbStr('NOTE') +
						__('Note that the tab number {i} starts at 0.', 'arkhe-blocks')}
				</div>
			</div>
			<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
				<BaseControl>
					<BaseControl.VisualLabel>
						{__('Tab Arrangement', 'arkhe-blocks')}
					</BaseControl.VisualLabel>
					<ButtonGroup className='arkb-btns--boxLayout'>
						{TAB_ALIGNS.map((_btn) => {
							const btnVal = _btn.value;
							return (
								<Button
									key={`align-${btnVal}`}
									text={_btn.label}
									isPrimary={tabAlign === btnVal}
									onClick={() => {
										setAttributes({ tabAlign: btnVal });
									}}
								/>
							);
						})}
					</ButtonGroup>
				</BaseControl>
				<ArkbProOnly>
					<TextControl
						label={__('Number of tabs to open first', 'arkhe-blocks')}
						type='number'
						min='1'
						max={tabHeaders.length}
						style={{ maxWidth: '6em' }}
						value={activeTab + 1}
						help={__(
							'If a URL parameter is specified, it takes precedence.',
							'arkhe-blocks'
						)}
						onChange={(val) => {
							if (!IS_PRO) return;
							const newActiveNum = parseInt(val || 1) - 1;
							setAttributes({ activeTab: newActiveNum });

							const tabBodyIDs = getBlockOrder(clientId);
							tabBodyIDs.forEach((_tabBodyID) => {
								updateBlockAttributes(_tabBodyID, {
									activeTab: newActiveNum,
								});
							});
						}}
					/>
				</ArkbProOnly>
				<ArkbDeviceTab
					// className=''
					controlPC={
						<>
							<RadioControl
								label={__('Tab width', 'arkhe-blocks')}
								selected={tabWidthPC}
								options={[
									...TAB_WIDTH_OPTIONS,
									{
										label: __('Fixed width', 'arkhe-blocks') + '(25%)',
										value: 'fix',
									},
								]}
								onChange={(val) => {
									setAttributes({ tabWidthPC: val });
								}}
							/>
							<ToggleControl
								label={__('Make it scrollable', 'arkhe-blocks') + '(PC)'}
								checked={isScrollPC}
								onChange={(value) => {
									setAttributes({ isScrollPC: value });
								}}
							/>
						</>
					}
					controlSP={
						<>
							<RadioControl
								label={__('Tab width', 'arkhe-blocks')}
								selected={tabWidthSP}
								options={[
									...TAB_WIDTH_OPTIONS,
									{
										label: __('Fixed width', 'arkhe-blocks') + '(50%)',
										value: 'fix',
									},
								]}
								onChange={(val) => {
									setAttributes({ tabWidthSP: val });
								}}
							/>
							<ToggleControl
								label={__('Make it scrollable', 'arkhe-blocks') + '(SP)'}
								checked={isScrollSP}
								onChange={(value) => {
									setAttributes({ isScrollSP: value });
								}}
							/>
						</>
					}
				/>
			</PanelBody>
			{/* <PanelBody title={__('Other settings', 'arkhe-blocks')} initialOpen={true}>
			</PanelBody> */}
		</>
	);
};
